import titleimg from '../common/login/titileImg.vue'
import { mapMutations ,mapGetters} from 'vuex'
import api from "../../api/login/login.js"
export default {
      data(){
            return{
                items: [],
                classItem:{},
                cell:null,
                trues:null,
                keshiv:false
            }

        },
        // beforeCreate(){
           
        // },
	components:{
		  titleimg
		},
		 created:function(){
            document.title="科室选择";
            var _this = this;
             api.sendoffice(2).then(function(json){
              // console.log(json.data.data)
              _this.items = json.data.data;
               _this.cell = _this.items[0].name;
              console.log(_this.items);
           })

        },  
        computed:{
            ...mapGetters({
              
            })
        },
        methods:{
            ...mapMutations([
               'USER_HOME',

            ]),
            homeChoose(items,event,bigCode,smallCode){
                 this.trues = items;
                 var _this = this;
                 var name = {
                    text:items,
                    id : event,
                    bigCode:bigCode,
                    smallCode:smallCode
                 }
                 _this.USER_HOME(name);
                setTimeout(function(){
                      _this.$router.push("/inft")
                    },100)
               
            },
            todo:function(key){
              this.cell = key;
            }
        }
}




